<template>
	<div class="app-container">
		<el-alert title="注意:处理过程约耗时0~5分钟，请稍后查询" show-icon type="warning">
		</el-alert>
		<inputs-header :options="options" :isAdmin="true" @change='changeCompany' @btnClick="checkIccid" class="clearfix"></inputs-header>
		<el-table :data="tableData" border style="width: 100%" v-loading="isLoad">
			<el-table-column prop="number" label="接入号码" width="150" align="center">
			</el-table-column>
			<el-table-column prop="iccid" label="ICCID" width="150" align="center">
			</el-table-column>
			<el-table-column prop="type" label="预警类型" width="150" align="center">
			</el-table-column>
			<el-table-column prop="time" label="时间" width="150" align="center">
			</el-table-column>
			<el-table-column prop="content" label="预警内容" align="center">
			</el-table-column>
		</el-table>
		<div style="text-align:center;margin: 20px auto;">
			<el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
			</el-pagination>
		</div>
	</div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import InputsHeader from 'components/InputsHeader/index';
import {
	getListWarningInfoByMid,
	getListWarningInfoByIccid
} from 'api/admin/trafficAlarmInfo';
import {
	getListManufacturerGeneral,
	getListIotCardByMatch,
	giveManufacturerAuthority
} from 'api/admin/usual';

import { backCN } from 'utils/index';

export default {
	data() {
		return {
			iccidInput: '',
			iccidArr: [],
			value8: '',
			options: [],
			csOptions: [],
			tableData: [],
			tableCount: 0,
			currentPage: 1,
			num: 10,
			isLoad:true
		}
	}, 
	watch: {},
	mounted() {
		this.initHeader()
	},
	methods: {
			initHeader() {
			this.isLoad = true
			getListManufacturerGeneral().then(res => {
				res.msg === 0 ? this.options = res.mList : this.$message.error(backCN(res.code));
				res.msg != 0 ? this.isLoad = false : ''
			}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			})
		},
		pageChange(page) {
			this.iccidInput ? this.ListWarningInfoByIccid(this.iccidInput, page, this.num)
				: this.ListWarningInfoByMid(page, this.num)
		},
		checkIccid(iccid) {
			this.iccidInput = iccid || ''
			iccid ? this.ListWarningInfoByIccid(iccid, 1, this.num)
				: this.ListWarningInfoByMid(1, this.num)
		},
		changeCompany(mid) {
			giveManufacturerAuthority({mid}).then(res => {
					if (res.msg === 0) {
						this.iccidInput ? this.ListWarningInfoByIccid(this.iccidInput, 1, this.num)
							: this.ListWarningInfoByMid(1, this.num)
					} else {
						this.isLoad = false
						this.$message.error(backCN(res.code));
					}
				}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});
			//				getListManufacturerGeneral();
		},
		ListWarningInfoByMid(page, num) {
			this.isLoad=true;
			getListWarningInfoByMid({ page, num }).then(res => {
				this.isLoad=false
				if (res.msg === 0) {
					this.tableData = res.warningList
					page === 1 ? this.tableCount = res.count : ''
				} else {
					this.tableData = []
					this.tableCount = 0
				}
				(res.msg != 0 && res.msg != 1) ? this.$message.error(backCN(res.code)) : ''
			}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});
		},
		ListWarningInfoByIccid(iccid, page, num) {
			this.isLoad=true
			getListWarningInfoByIccid({ iccid, page, num }).then(res => {
				this.isLoad=false
				if (res.msg === 0) {
					this.tableData = res.warningList
					page === 1 ? this.tableCount = res.count : ''
				} else {
					this.tableData = []
					this.tableCount = 0
				}
				(res.msg != 0 && res.msg != 1) ? this.$message.error(backCN(res.code)) : ''

			}).catch(err => {
				this.isLoad = false
				this.$message.error(backCN(err.status))
			});
		}
	},
	components: {
		InputsHeader
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}

.clearfix:before,
.clearfix:after {
	display: table;
	content: "";
}

.clearfix:after {
	clear: both
}

.box-card {
	width: 100%;
	min-width: 637px;
	margin-bottom: 20px;
}

.survey-container {}

.survey-item-wrap {
	padding: 5px 0;
}

.survey-item {
	border-right: 1px solid #e0e0e0;
}

.survey-item-wrap:last-child .survey-item {
	border: none;
}

.survey-item h3 {
	font-size: 13px;
	color: #666;
}

.survey-item p {
	color: #f88b00;
	font-size: 25px;
}

.content-rowspan div {
	padding-left: 13px;
	line-height: 46px;
	border-bottom: 1px solid #ECEDEE;
}

.extend .cell {
	padding: 0;
}

.flow-details {
	border-bottom: 1px solid #e8e8e8;
}

.flow-details:last-child {
	border: none;
}
</style>


